.ctrl-lable .hide {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #fff;
    border-radius: 3px;
    border: 1px solid #c7c1c1;
}
.ctrl-lable:hover .hide ~ .checkmark {
    background-color: #eee;
}
.ctrl-lable .hide:checked ~ .checkmark {
    background-color: #3b5998;
    -webkit-transition: 0.2s linear;
    transition: 0.2s linear;
    border: 1px solid #3b5998;
}
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}
.ctrl-lable .hide:checked ~ .checkmark:after {
    display: block;
}
.ctrl-lable .checkmark:after {
    left: 7px;
    top: 3px;
    width: 6px;
    height: 10px;
    border: solid #fff;
